<template>
  <div class="page">
    <index-top 
      :h1="'KISwap'"
      :h4="$t('index.最具潜力的集DEX、IKO、DAO、聚合交易为一体的DEFI生态平台')"></index-top>
    <div class="list flex-wrap">
      <div class="item" v-for="item in record">
        <div class="item-top flex-center">
          <img :src="$tokenLogo(kis)" alt="" class="icon-logo">
          <img src="@/assets/image/light/trading/tra.png" alt="" class="icon-tra" v-if="themeMode === 'light'">
          <img src="@/assets/image/trading/tra.png" alt="" class="icon-tra" v-else>
          <img :src="$tokenLogo(item.tokenAddress)" alt="" class="icon-logo">
        </div>
        <div class="item-tit">KIS/{{item.symbol}}</div>
        <div class="item-info">
          <p>{{$t('ieo.每KIS兑换个', {num: item.priceUsdt, symbol: item.symbol})}}</p>
          <p>{{$t('ieo.总量个', {num: item.tokenTotal})}}&emsp;{{$t('ieo.当前剩余个', {num: Number(item.tokenTotal-item.tokenSpend).toFixed(2)})}}</p>
          <p>{{$t('ieo.开始时间')}}：{{item.startTime}}---{{item.stopTime}}</p>
        </div>
        <router-link class="btn" :to="{name: 'IeoExchange', params: {id: item.id}}">{{$t('index.兑换')}}</router-link>
      </div>
    </div>
    <router-link v-if="canAdd" class="add-btn" :to="{name: 'IeoAdd'}">{{$t('ieo.添加')}}</router-link>
  </div>
</template>
<script>
import IndexTop from "@/views/index/IndexTop";
import { reactive, toRefs, computed, watch} from "vue";
import ieoAbi from "@/utils/ieoAbi";
import BigNumber from "bignumber.js";
import tokenAbi from "@/utils/abi";
import Dateformat from "dateformat";
import {useStore} from "vuex";
import config from "@/utils/config";
export default {
  components: {
    IndexTop
  },
  setup () {
     const store = useStore();
     const data = reactive({
       themeMode: computed(() => store.state.themeMode),
       rate: computed(() => store.state.rate),
       web3: computed(() => store.state.provider),
       address: computed(() => store.state.accounts[0]),
       ieoContract: null,
       tokenContract: null,
       kis: config.kisAddress,
       canAdd: false,
       record: [],
       initContract: () => {
         data.ieoContract = new data.web3.eth.Contract(ieoAbi, config.ieo)
         data.tokenContract = new data.web3.eth.Contract(tokenAbi, config.kisAddress)
         data.getList()
         data.getOwner()
       },
       getList: async () => {
         console.log(data.ieoContract)
         let project = await data.ieoContract.methods.getProjectIds().call()
         console.log(project)
         project.forEach(async item => {
           let projectItem = await data.ieoContract.methods.getProjectById(item).call()
           let tokenContract = new data.web3.eth.Contract(tokenAbi, projectItem.tokenAddress)
           let decimals = await tokenContract.methods.decimals().call();
           let symbol = await tokenContract.methods.symbol().call();
           let isStop = await data.ieoContract.methods.isStop(item).call()
           let params = {
             id: item,
             symbol: symbol,
             isStop: isStop,
             tokenSpend: new BigNumber(projectItem.tokenSpend).dividedBy(Math.pow(10, decimals)).toFixed(2),
             startTime: Dateformat(new Date(projectItem.startTime * 1000), "yyyy-mm-dd hh:MM"),
             stopTime: Dateformat(new Date(projectItem.stopTime * 1000), "yyyy-mm-dd hh:MM"),
             tokenTotal: new BigNumber(projectItem.tokenTotal).dividedBy(Math.pow(10, decimals)).toFixed(2),
             priceUsdt: projectItem.priceUsdt,
             haveCollect: new BigNumber(projectItem.haveCollect).dividedBy(Math.pow(10, 18)).toFixed(2),
             isEnd: projectItem.isEnd,
             tokenAddress: projectItem.tokenAddress
           }
           console.log(params)
           data.record.unshift(params)
         });
       },
       getOwner: async () => {
         let owner =  await data.ieoContract.methods.owner().call()
        if (owner.toLowerCase() == data.address.toLowerCase()) {
          data.canAdd = true
        }
       }
     })
    
     if (data.address) {
       data.initContract()
     }
     watch(() => data.address, (val) => {
       if (val) {
         data.initContract()
       }
     })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="scss" scoped>
.add-btn {
  display: block;
  width: 331px;
  height: 72px;
  font-size: 18px;
  font-weight: bold;
  color: #fff !important;
  text-align: center;
  line-height: 72px;
  margin: 40px auto 80px;
  background: url('~@/assets/image/liquidity/btn_bg3.png') no-repeat center;
  background-size: 100% 100%;
  cursor: pointer;
  transition: all .3s;
  &:hover {
    opacity: 0.8;
  }
}
.list {
  max-width: 1048px;
  min-height: 458px;
  margin: 80px auto 40px;
  .item {
    width: 524px;
    min-height: 458px;
    background: url('~@/assets/image/ieo/border1.png') no-repeat 0 0;
    background-size: 100% 100%;
    padding: 70px 40px 60px;
    color: #E9E9E9;
    .item-top {
      .icon-logo {
        width: 62px;
        height: 62px;
      }
      .icon-tra {
        width: 53px;
        margin: 0 23px;
      }
    }
    .item-tit {
      font-size: 28px;
      color: #E9E9E9;
      text-align: center;
      line-height: 1;
      font-weight: bold;
      margin: 21px 0 15px;
    }
    .item-info {
      font-size: 18px;
      line-height: 36px;
      text-align: center;
    }
    .btn {
      display: block;
      width: 399px;
      height: 61px;
      line-height: 61px;
      background: url('~@/assets/image/liquidity/btn_bg2.png') no-repeat 0 0;
      background-size: 100% 100%;
      margin: 22px auto 0;
      text-align: center;
      font-size: 18px;
      font-weight: bold;
      cursor: pointer;
      transition: all 0.3s;
      color: #fff !important;
      &:hover {
        background-image: url('~@/assets/image/liquidity/btn_bg3.png');
      }
    }
  }
}
.theme-light {
  .add-btn {
    color: #AD7805 !important;
    background-image: url('~@/assets/image/light/liquidity/btn_bg3.png');
  }
  .list {
    .item {
      color: #666;
      background-image: url('~@/assets/image/light/ieo/border1.png');
      .item-tit {
        color: #333;
      }
      .btn {
        background-image: url('~@/assets/image/light/liquidity/btn_bg2.png');
        color: #3A7343 !important;
        &:hover {
          color: #AD7805 !important;
          background-image: url('~@/assets/image/light/liquidity/btn_bg3.png');
        }
      }
    }
  }
}
@media screen and (max-width: 480px) {
  .add-btn {
    width: 90%;
    height: 60px;
    line-height: 60px;
    font-size: 16px;
    margin: 30px auto;
  }
  .list {
    margin: 50px 0 30px;
    .item {
      width: 100%;
      min-height: auto;
      padding: 50px 30px 50px;
      .item-top {
        .icon-logo {
          width: 44px;
          height: 44px;
        }
        .icon-tra {
          width: 32px;
        }
      }
      .item-tit {
        font-size: 22px;
        margin-bottom: 10px;
      }
      .item-info {
        font-size: 14px;
        line-height: 24px;
      }
      .item-lv {
        font-size: 16px;
      }
      .btn {
        width: 100%;
        height: 50px;
        line-height: 50px;
        font-size: 16px;
      }
    }
  }
}
</style>
